import uniPopup from '@/components/uni-popup/uni-popup.vue'
import md5 from '../../static/js/md5.js';
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			user_info:[],
			type: '',
			money:'0.0',
			dues_info:[],
			signStr:'',
			timestamp:'',
			userInfo:false,
			type:'',
			addClick:true,
			openid:'',
		}
	},
	onLoad() {
		var t=this;
		uni.showLoading();
		t.urlRequest.urlRequest(t.func.getFeeDetail,{communist_no:t.globalData.userInfo.communist_no},function(res){
			if(res.data.status==0){
				uni.showToast({
					title: '暂无缴费信息',
					duration: 2000,
					icon:'none',
					success:function(){
						uni.hideLoading();
						setTimeout(function(){
							uni.switchTab({
								url: "index"
							})
						},2000)
					}
				});
			}else{
				if(res.data.dues_info.status==2){
					uni.hideLoading();
					uni.showToast({
						title: '本月费用已缴',
						duration: 2000,
						icon:'none',
						success:function(){
							setTimeout(function(){
								uni.switchTab({
									url: "index"
								})
							},2000)
						}
					});
				}else{
					t.money=res.data.dues_info.dues_amount;
					t.dues_info=res.data.dues_info
					uni.hideLoading();
					

						t.type='MP-WEIXIN';




				}
			}
			
		})
		//判断当前用户是否登录
		uni.getStorage({
			key: 'user_openid',
			success: (res) => {
				console.log(res)
				if(res.data1=''){
					t.openid=res.data
				}else{
					t.userInfo=true;
				}
			},
			fail:()=>{
				t.userInfo=true;
			}
		})
	},
	methods: {
		payFee:function(){
			uni.showLoading();
			var t=this;
			
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				money:t.money,
				dues_id:t.dues_info.dues_id,
				openid:t.openid
			}
			t.urlRequest.urlRequest('http://wx.kocelcloud.com/dj-h5/mini/WeiMiniPay/doalipay',data,function(res){
				let paymentData = res.data;
				var timeStamp=String(paymentData.timestamp);
				var nonceStr=paymentData.noncestr;
				var pkg='prepay_id='+paymentData.prepayid;
				
				var signStr = 'appId=' + t.globalData.appid + '&nonceStr=' + nonceStr + '&package=' + pkg + '&signType=MD5' + '&timeStamp=' + timeStamp + "&key=" + t.globalData.mch_idSecret;
				
				var paySign = md5(signStr).toUpperCase();//此处用到hexMD5插件
				uni.requestPayment({
					timeStamp: timeStamp,
					nonceStr: nonceStr,
					package: pkg,
					signType: 'MD5',
					paySign: paySign,
					success: (res) => {
						uni.showToast({
							title: "付款成功!",
							duration: 2000,
							icon:'none',
							success:function(){
								setTimeout(function(){
									uni.switchTab({
										url: 'index',
									})
								},2000)
							}
						})
						
						uni.hideLoading({
							success:function(){
								t.addClick=true;
							}
						});
					},
					fail: (res) => {
						uni.showModal({
							content: "支付失败",
							showCancel: false
						})
						uni.hideLoading({
							success:function(){
								t.addClick=true;
							}
						});
					},
					complete: () => {
						t.loading = false;
					}
				})
			})
		},
		getUserInfo:function(){
			var t=this;
			uni.login({
				success: (res) => {
					t.urlRequest.urlRequest('http://wx.kocelcloud.com/dj-h5/mini/WeixinMember/getOpenid',{code:res.code},function(member){
					// t.urlRequest.urlRequest(t.func.getOpenid,{code:res.code},function(member){
						t.openid=member.data;
						if(member.data){
							uni.setStorage({
								key: 'user_openid',
								data:member.data,
								success: (user) => {
									uni.showToast({
										title: '登录成功',
										duration: 2000,
										icon:'none',
									});
									t.userInfo=false;
								},
								fail: () => {
									uni.showToast({
										title: '登录失败,退出重试',
										duration: 2000,
										icon:'none',
									});
								}
							})
						}
						
					})
					
				}
			});
		},
		togglePopup(type) {
			this.type = type
		},
		doPay:function(type){
			var t=this;
			t.togglePopup();
			uni.showLoading();
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				money:t.money,
				dues_id:t.dues_info.dues_id
			}
			//type为1表示微信支付
			if(type==1){
				window.location.href=t.func.WeiPay+'?communist_no='+data.communist_no+'&money='+data.money+'&dues_id='+data.dues_id
				uni.hideLoading();
			}else if(type==2){
				if(t.dues_info.openid==0){
					uni.getStorage({
						key: 'user_info',
						success: function (res) {
							window.location.href=t.func.WeiUserIndex+'?communist_no='+res.data.communist_no
						},
					});
				}else{
					t.urlRequest.urlRequest(t.func.WeiUserPay,data,function(res){
						uni.hideLoading();
						let [appId, timestamp,mch_idSecret, nonceStr, signature,pkg] = [res['data']['jsapi']['appId'], res['data']['jsapi']['timestamp'],res['data']['jsapi']['mch_idSecret'], res['data']['jsapi']['nonceStr'], res['data']['jsapi']['signature'],
						        'prepay_id='+res['data']['order']['prepayid']
						    ];
							
							var signStr = 'appId=' + appId + '&nonceStr=' + nonceStr + '&package=' + pkg + '&signType=MD5' + '&timeStamp=' + timestamp + "&key=" + mch_idSecret;
							
							var paySign = md5(signStr).toUpperCase();//此处用到hexMD5插件
							
						    wx.config({
						        debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
						        appId, // 必填，公众号的唯一标识
						        timestamp, // 必填，生成签名的时间戳
						        nonceStr, // 必填，生成签名的随机串
						        signature, // 必填，签名，见附录1
						        jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
						    });
						    wx.ready(function() {
						        wx.chooseWXPay({
						            timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
						            nonceStr, // 支付签名随机串，不长于 32 位
						            'package': pkg, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
						            signType: 'MD5', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
						            paySign, // 支付签名
						            success(res) {
						                uni.showToast({
						                	title: '支付成功',
						                	duration: 2000,
						                	icon:'none',
						                	success:function(){
												setTimeout(function(){
													uni.switchTab({
														url: "index"
													})
												},2000)
											}
						                });
						            },
						            fail(res) {
						                uni.showToast({
						                	title: '支付失败',
						                	duration: 2000,
						                	icon:'none',
						                	success:function(){
						                		setTimeout(function(){
						                			uni.switchTab({
						                				url: "index"
						                			})
						                		},2000)
						                	}
						                });
						            }
						        });
						    });
						 
						    wx.error(function(res) {
						        // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
						        /*alert("config信息验证失败");*/
						    });
					})
				}
			}else{
				window.location.href=t.func.AliPay+'?communist_no='+data.communist_no+'&money='+data.money+'&dues_id='+data.dues_id
				uni.hideLoading();
			}
			
			
		}
	}
}
